.payment__title {
  display: none;
}
.payment__promo {
  width: 240px;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 0;
  padding-left: 10px;
  line-height: 26px;
  font-size: 21px;
}
.payment__link-list {
  display: none;
}
.payment__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.payment__list-item {
  position: relative;
  padding: 16px;
  padding-right: 18px;
  padding-top: 14px;
  padding-bottom: 18px;
  list-style: none;
  border-bottom: 1px solid #cedae4;
}
.payment__list-item::after {
  position: absolute;
  content: "";
  width: 0;
  height: 1px;
  top: 24px;
  right: 16px;
  border: 4px solid transparent;
  border-top: 7px solid #0777ba;
  border-bottom: none;
}
.payment__list-item--active::after {
  top: 26px;
  right: 15px;
  border-bottom: 7px solid #0777ba;
  border-top: none;
}
.payment__list-item::before {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  top: 0;
  left: 11px;
  background-repeat: no-repeat;
  background-position: center;
}
.payment__list-item:nth-child(1)::before {
  background-image: url("../img/gibdd-icon.png");
}
.payment__list-item:nth-child(2)::before {
  background-image: url("../img/gibdd2-icon.png");
  background-position: 13px 13px;
}
.payment__list-item:nth-child(3)::before {
  background-image: url("../img/court-icon.png");
  background-position: 9px 9px;
}
.payment__list-item:nth-child(4)::before {
  background-image: url("../img/tax-icon.png");
}
.payment__list-item:nth-child(5)::before {
  background-image: url("../img/rosreestr-icon.png");
  background-position: 11px 11px;
}
.payment__list-item-title {
