  .footer__nav-link,
  .footer__contact-link {
    color: #0677b9;
    line-height: 21px;
    font-size: 15px;
  }
  .footer__nav-link:hover,
  .footer__contact-link:hover {
    text-decoration: none;
    color: red;
  }
  .footer__contact-links-list {
    display: block;
    flex-shrink: 0;
    margin: 0;
    margin-left: 5px;
    padding: 0;
    list-style: none;
  }
  .footer__additional-copyrights-wrapper {
    display: block;
    text-align: right;
    margin-left: 4.6%;
  }
  .footer__additional-copyrights-links--desktop {
    width: 230px;
    flex-shrink: 1;
    text-align: right;
    line-height: 21px;
    font-size: 15px;
    margin-bottom: 15px;
  }
  .footer__additional-copyrights-links--desktop a {
    line-height: 21px;
    font-size: 15px;
  }
}
.modal-login--hidden {
  display: none;
}
.modal-login {
  position: fixed;
  box-sizing: border-box;
  width: 320px;
  padding: 33px 40px;
  padding-bottom: 46px;
  margin-left: -160px;
  top: 20%;
  left: 50%;
  background-color: #ffffff;
  z-index: 10;
}
.modal-login__text {
  max-width: 265px;
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
}
.modal-login__input-name,
.modal-login__input-password {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  margin: 0;
  margin-bottom: 19px;
  padding: 18px;
  border: none;
  line-height: 26px;
  font-size: 18px;
  box-shadow: inset 2px 2px 4px -1px #000000;
}
.modal-login__input-name:focus,
.modal-login__input-password:focus {
  box-shadow: inset 2px 2px 5px -1px grey;
}
.modal-login__enter {
  display: block;
  width: 100%;
  height: 60px;
  margin: 0;
  margin-bottom: 37px;
  padding: 0;
  border: none;
  color: #ffffff;
  background-color: #d30531;
  line-height: 26px;
  font-size: 20px;
  box-shadow: inset 0 0 5px -1px #000000, 2px 2px 5px -1px #000000;
}
.modal-login__enter:hover {
  box-shadow: inset 0 0 5px -1px #000000, 2px 2px 5px -1px grey;
}
.modal-login__enter:active {
  box-shadow: inset 2px 2px 5px -1px grey, 0 0 5px -1px #000000;
}
.modal-login__forget-password,
.modal-login__registration {
  display: inline-block;
  margin-right: 110px;
  color: #0777ba;
  line-height: 18px;
  font-size: 15px;
  text-decoration: none;
  border-bottom: 1px dotted #0777ba;
}
.modal-login__forget-password:hover,
.modal-login__registration:hover {
  color: red;
  text-decoration: none;
}
.modal-login__forget-password {
  margin-bottom: 28px;
}
.modal-login__close-button {
  position: absolute;
  top: 1px;
  right: 2px;
  width: 30px;
  height: 30px;
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
}
.modal-login__close-button::before,
.modal-login__close-button::after {
  content: "";
  position: absolute;
  width: 25px;
  height: 2px;
  top: 13px;
  right: 5px;
  background-color: #000000;
  transform: rotate(45deg);
}
.modal-login__close-button::after {
  transform: rotate(-45deg);
}
@media (min-width: 769px) {
  .modal-login {
    width: 400px;
    margin-left: -200px;
  }
  .modal-login__forget-password,
  .modal-login__registration {
    display: inline-block;
    margin-right: 180px;
  }
  .modal-login__close-button {
    right: -50px;
  }
  .modal-login__close-button::before,
  .modal-login__close-button::after {
    background-color: #ffffff;
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 7;
}
.overlay--hidden {
  display: none;
}
